.footerParentDiv {
	width: 100%;
}
.footerParentDiv .content {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background: #ebeeef;
	padding: 16px;
}
.footerParentDiv .content .heading {
	font-size: 24px;
	font-weight: bolder;
}

.footerParentDiv .footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 10px;
	background: #002f34;
	color: whitesmoke;
}

.footerParentDiv .footer p {
	margin: 0;
}
