.headerParentDiv {
	padding: 10px;
	background-color: whitesmoke;
	position: fixed;
	width: 100%;
	z-index: 999;
}

.headerChildDiv {
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.placeSearch {
	width: 16em;
	height: 48px;
	display: flex;
	align-items: center;
	padding: 0 8px;
	border: 2px solid #002f34;
	border-radius: 4px;
	background-color: white;
}

.placeSearch ::selection {
	border-color: #23e5db;
	outline-color: #23e5db;
}

.placeSearch input {
	border-color: transparent;
	outline-color: transparent;
}

.productSearch {
	width: 35em;
	height: 48px;
	border: 2px solid #002f34;
	border-radius: 4px;
	display: flex;
	align-items: center;
	background-color: white;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.productSearch .input {
	width: 100%;
	height: 100%;
	display: flex;
}

.productSearch .input input {
	border-color: transparent;
	outline-color: transparent;
	width: 100%;
	align-items: center;
}

.productSearch .searchAction {
	min-width: 48px;
	height: 48px;
	position: relative;
	display: flex;
	cursor: pointer;
	background-color: #002f34;
	border-radius: 0 4px 4px 0;
}

.productSearch .searchAction svg {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	fill: white;
}

.language {
	height: 48px;
	display: flex;
	align-items: center;
}

.language span {
	/* justify-content: space-between;
  align-items: center; */
	font-size: 15px;
	white-space: normal;
	text-overflow: ellipsis;
	font-weight: bolder;
	margin-right: 5px;
}

.language svg {
}

.span {
	text-decoration: none;
	font-size: 16px;
	white-space: normal;
	text-overflow: ellipsis;
	font-weight: bolder;
	margin-right: 5px;
}

.loginPage hr {
	margin: 0;
	background-color: black;
	height: 2px;
}

.loginPage a {
	text-decoration: none;
}

.sellMenu {
	position: relative;
	cursor: pointer;
}

.sellMenu .all {
	fill: white;
}

.sellMenu .top {
	fill: #23e5db;
}

.sellMenu .left {
	fill: #ffce32;
}

.sellMenu .right {
	fill: #3a77ff;
}
.sellMenu .sellMenuContent {
	display: flex;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	font-size: 14px;
	font-weight: 700;
	letter-spacing: 0.5px;
	color: #002f34;
}

/* .sellMenu .sellMenuContent svg {
  align-items: center;
} */
.sellMenu .sellMenuContent span {
	margin-left: 5px;
}

@media only screen and (max-width: 700px) {
	.placeSearch,
	.productSearch,
	.sellMenu {
		display: none;
	}
}
